import React, { useState, useEffect } from "react";
import { CSSTransition } from "react-transition-group";
import Image from "../components/elements/Image";
import { TickerTitle } from "../components/elements/Ticker";
import Marquee from "react-fast-marquee";
import Button from "react-bootstrap/Button";

//Swiper
import SwiperCore, {
  Navigation,
  Pagination,
  Scrollbar,
  A11y,
  EffectCoverflow,
} from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper.scss";
import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";
import "swiper/components/scrollbar/scrollbar.scss";
import "swiper/components/effect-coverflow/effect-coverflow.scss";
import { ticker_data } from "../data/EventTicker";
SwiperCore.use([Navigation, Pagination, Scrollbar, A11y, EffectCoverflow]);

const EVENT_NAMES = [
  "topic",
  "zodiac",
  "trend",
  "iad",
  "brand",
  "resume",
  "top100",
];

const TICKER_NUM = 5;

const swiper_params = {
  spaceBetween: 50,
  pagination: { clckable: true },
  effect: "coverflow",
  slidesPerView: 1,
  centeredSlides: true,
  navigation: true,
  coverflowEffect: {
    rotate: 0,
    stretch: 0,
    depth: 80,
    modifier: 1,
    slideShadows: true,
  },
};

const Event = (props) => {
  const [show, setShow] = useState(false);
  const [activeEvent, setActiveEvent] = useState("topic");

  useEffect(() => {
    setShow(true);
    document.body.style.backgroundColor = "white";
  });

  function getHeaderArrow() {
    const window_width = window.innerWidth;
    const window_height = window.innerHeight;
    // const arrow_height = window_height * 0.012;
    // const arrow_ratio = 3.4;
    // const arrow_width = arrow_height * arrow_ratio;
    const arrow_width = (window_height * 1.2 * 3.4) / 100;
    const expected_width = (window_width * 16) / 36;
    const arrow_num = expected_width / arrow_width;
    var arrow_rows = [];
    for (var i = 0; i < arrow_num; i++) {
      arrow_rows.push(
        <Image
          key={i}
          className="moth-event-arrow"
          src={require(`./../assets/images/header_arrow.png`)}
        />
      );
    }

    return <>{arrow_rows}</>;
  }

  function handleSlideChanged(swiper) {
    setActiveEvent(EVENT_NAMES[swiper.activeIndex]);
    // console.log(activeEvent);
  }

  function handleSubmit() {
    props.history.push("/words");
  }

  // little hack : repeat(3) to let the string longer for Marqeuee
  const ticker_title = [];
  const ticker_content = [];
  for (var i = 0; i < TICKER_NUM; i++) {
    ticker_title.push(ticker_data[activeEvent]["title"][i]);
    ticker_content.push(ticker_data[activeEvent]["content"][i].repeat(3));
  }
  return (
    <CSSTransition in={show} timeout={1000} classNames="landing">
      <main className="site-content">
        <section style={{ height: "5vh" }}></section>
        <div
          className="event-header-arrow-text-container"
          style={{ height: "7vh" }}
        >
          <span className="header-arrow-container">{getHeaderArrow()}</span>
          <div className="header-text-container">選擇一件你</div>
          <div className="header-text-container">
            <div className="header-text-event-special">最關心</div>的事吧！
            <br />
            <div className="header-text-event-special">最喜歡</div>
            <br />
            <div className="header-text-event-special">最有共鳴</div>
            <br />
            <div className="header-text-event-special">最感興趣</div>
          </div>
        </div>
        {/* <section style={{ height: "10vh" }}></section> */}
        <section className="upper-event-grid" style={{ height: "20vh" }}>
          <div className="ticker1" style={{ height: "10vh" }}>
            <TickerTitle height="75%" data={ticker_title[0]} />
            <Marquee gradientWidth={"5vw"} speed={40}>
              <div className="marquee-content">{ticker_content[0]}</div>
            </Marquee>
          </div>
          <div className="ticker2" style={{ height: "10vh" }}>
            <TickerTitle height="75%" data={ticker_title[1]} />
            <Marquee gradientWidth={"5vw"} speed={40}>
              <div className="marquee-content">{ticker_content[1]}</div>
            </Marquee>
          </div>
        </section>
        <section className="event-swiper" style={{ height: "30vh" }}>
          <Swiper
            {...swiper_params}
            onSlideChange={(swiper) => handleSlideChanged(swiper)}
            // onSwiper={(swiper) => console.log(swiper)}
          >
            {EVENT_NAMES.map((event_name, idx) => (
              <SwiperSlide key={idx}>
                <Image
                  className="swiper-event-image"
                  src={require(`./../assets/images/events/${event_name}.png`)}
                />
              </SwiperSlide>
            ))}
          </Swiper>
        </section>
        <section className="lower-event-grid" style={{ height: "30vh" }}>
          <div className="ticker3" style={{ height: "10vh" }}>
            <Marquee gradientWidth={"5vw"} speed={40}>
              <div className="marquee-content">{ticker_content[2]}</div>
            </Marquee>
            <TickerTitle height="75%" data={ticker_title[2]} />
          </div>
          <div className="ticker4" style={{ height: "10vh" }}>
            <Marquee gradientWidth={"5vw"} speed={40}>
              <div className="marquee-content">{ticker_content[3]}</div>
            </Marquee>
            <TickerTitle height="75%" data={ticker_title[3]} />
          </div>
          <div className="ticker5" style={{ height: "10vh" }}>
            <Marquee gradientWidth={"5vw"} speed={40}>
              <div className="marquee-content">{ticker_content[4]}</div>
            </Marquee>
            <TickerTitle height="75%" data={ticker_title[4]} />
          </div>
        </section>
        <section style={{ height: "10vh" }}>
          <div className="moth-footer-container">
            <Button
              variant="outline-dark"
              className="moth-footer-button"
              onClick={() => {
                handleSubmit();
              }}
            >
              下一步！
            </Button>
          </div>
        </section>
      </main>
    </CSSTransition>
  );
};
// css grid ref
// https://www.w3schools.com/cssref/tryit.asp?filename=trycss_grid-area
export default Event;
