import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { CSSTransition } from "react-transition-group";

const propTypes = {
  pageNumber: PropTypes.number,
};

//control if header shows
const defaultProps = {
  pageNumber: 1,
};

const LayoutDefault = ({ children, pageNumber }) => {
  const [show, setShow] = useState(false);
  useEffect(() => {
    setShow(true);
  });

  const getLayout = (pageNumber) => {
    switch (pageNumber) {
      //landing
      case 1:
        return (
          <>
            <main className="site-content">{children}</main>
          </>
        );

      //events
      case 3:
        return (
          <>
            <div>
              <main className="site-content">{children}</main>
            </div>
          </>
        );

      //words
      case 4:
        return (
          <>
            \
            <div>
              <main className="site-content">{children}</main>
              {/* in order to send state to the next page, we put Footer in <main> */}
              {/* <Footer nxtPage="/result" /> */}
            </div>
          </>
        );
      // case 5 was for result, but now we use <Link/> so we don't need this
    }
  };
  return <>{getLayout(pageNumber)}</>;
};

LayoutDefault.propTypes = propTypes;
LayoutDefault.defaultProps = defaultProps;

export default LayoutDefault;
