import * as act from "../actions";

// initial state
const initial_state = {
  actives: [],
  activeWords: {
    A: [],
    B: [],
    C: [],
    D: [],
    E: [],
    F: [],
  },
  activeNums: {
    A: 0,
    B: 0,
    C: 0,
    D: 0,
    E: 0,
    F: 0,
  },
};

// reducer
const moth_words = (state = initial_state, action) => {
  switch (action.type) {
    case act.ACTIVES_UPDATE:
      return {
        ...state,
        actives: action.payload,
      };
    case act.ACTIVE_NUMS_UPDATE:
      return {
        ...state,
        activeNums: action.payload,
      };
    case act.ACTIVE_WORDS_UPDATE:
      return {
        ...state,
        activeWords: action.payload,
      };
    default:
      return state;
  }
};

export default moth_words;
