import Ticker from "react-ticker";
import React from "react";

const TickerTitle = (props) => {
  return (
    <div className="ticker-title" style={{ height: props.height }}>
      {props.data}
    </div>
  );
};

// not using this for now 2021/04/24
const TickerContent = (props) => {
  return (
    <div className="ticker-content" style={{ height: props.height }}>
      <Ticker>
        {({ index }) => {
          return (
            <>
              <div className="ticker-text">{props.data}</div>
            </>
          );
        }}
      </Ticker>
    </div>
  );
};

export { TickerTitle, TickerContent };
