import React, { useState, useEffect } from "react";
import Image from "../components/elements/Image";
import parse from "html-react-parser";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { CSSTransition } from "react-transition-group";
import Button from "react-bootstrap/Button";

const result_text = parse(
  "在這個花花世界中<br />無論是和社會主流同向或反向而行<br /><br />無論是「虫」亦或是「我」<br />明確的知道自己<br />為何而行、為什麼行<br /><br />你，便是獨一無二的"
);
const result_color_text = parse(
  "蛾類的顏色分布與海拔高低有關，<br />因此將人類行為的起源環境做連結。"
);
const result_texture_text = parse(
  "蛾類的花紋疏密與物種數量多寡有關聯，<br />因此將人類與群體的互動程度相呼應。"
);
const result_burn_text = parse(
  "蛾類之間與人類間都存在著競爭關係，<br />選用燃燒造成的破碎程度呈現競爭的激烈與否。"
);

function getWords(L) {
  return (
    <tr>
      <td>
        {L.map((word, idx) => (
          <React.Fragment key={word.name}>
            <Button
              className="moth-words-button"
              variant="outline-dark"
              active={word.active}
            >
              {word.name}
            </Button>
            &thinsp;&thinsp;
          </React.Fragment>
        ))}
      </td>
    </tr>
  );
}

const Result = ({ className, ...props }) => {
  const { activeNums } = props.result_moth_words;
  const A = activeNums["A"];
  const B = activeNums["B"];
  const C = activeNums["C"];
  const D = activeNums["D"];
  const E = activeNums["E"];
  const F = activeNums["F"];

  const AB_number = (A - B).toString();
  const CD_number = C + D < 10 ? "0" + (C + D).toString() : (C + D).toString();
  const EF_number = (E - F).toString();
  const moth_number = AB_number + CD_number + EF_number;

  let [mothNumber, setMothNumber] = useState(moth_number);
  const [show, setShow] = useState(false);
  const [move, setMove] = useState(false);
  const [secondShow, setSecondShow] = useState(false);

  // localStorage funciton is directly from the browser
  // F12 -> application -> local storage
  // if the user refresh this page
  if (mothNumber === "0000") {
    //setMothNumber(localStorage.getItem("MOTHNUMBER"));
    mothNumber = localStorage.getItem("MOTHNUMBER");
  }

  // from words to here
  else {
    localStorage.setItem("MOTHNUMBER", mothNumber);
  }

  useEffect(() => {
    setShow(true);

    // 開始移動的等待時間
    const waitingTime = 4000;
    setTimeout(() => {
      setMove(true);
    }, waitingTime);

    setTimeout(() => {
      setSecondShow(true);
    }, waitingTime + 2000);
  }, []);

  return (
    <>
      <div className="site-content">
        <div className="container-xs">
          <CSSTransition in={show} timeout={2000} classNames="landing">
            <div>
              <section style={{ height: "5vh" }}></section>
              <section style={{ height: "40vh" }}>
                <div className="result-image-container">
                  <CSSTransition
                    in={move}
                    timeout={2000}
                    classNames="result-image"
                  >
                    <Image
                      className="result-image"
                      src={require(`./../assets/images/moths/${mothNumber}.png`)}
                    />
                  </CSSTransition>
                </div>
              </section>
            </div>
          </CSSTransition>

          <CSSTransition
            in={move}
            timeout={2000}
            classNames="result-overlay-color"
          >
            <div className="result-overlay-color"></div>
          </CSSTransition>

          <CSSTransition in={show} timeout={2000} classNames="landing">
            <CSSTransition in={move} timeout={2000} classNames="result-text">
              {/* height=30vh (in the class css) */}
              <section className="result-text-container">
                <div className="moth-text result-text">{result_text}</div>
              </section>
            </CSSTransition>
          </CSSTransition>

          <CSSTransition
            in={secondShow}
            timeout={2000}
            classNames="result-info"
          >
            <div className="result-info">
              <section>
                <div className="moth-result-chosen-container">
                  <div className="moth-result-chosen-title">
                    <div className="moth-result-chosen-text">顏色</div>
                    <span className="moth-result-chosen-arrow">
                      <Image
                        className="moth-result-dobule-arrow"
                        src={require("./../assets/images/results/double_arrow.png")}
                      />
                    </span>
                    <div className="moth-result-chosen-text">起源</div>
                  </div>

                  <div className="moth-words-table">
                    <table style={{ marginBottom: "0px" }}>
                      <tbody>
                        {getWords(
                          props.result_moth_words.activeWords["A"].concat(
                            props.result_moth_words.activeWords["B"]
                          )
                        )}
                      </tbody>
                    </table>
                  </div>
                  <Image
                    className="moth-result-chosen"
                    src={require(`./../assets/images/results/AB_color/color${AB_number}.png`)}
                  />
                  <div className="moth-text">{result_color_text} </div>
                </div>

                <div className="moth-result-chosen-container">
                  <div className="moth-result-chosen-title">
                    <div className="moth-result-chosen-text">花紋</div>
                    <span className="moth-result-chosen-arrow">
                      <Image
                        className="moth-result-dobule-arrow"
                        src={require("./../assets/images/results/double_arrow.png")}
                      />
                    </span>
                    <div className="moth-result-chosen-text">互動關係</div>
                  </div>

                  <div className="moth-words-table">
                    <table style={{ marginBottom: "0px" }}>
                      <tbody>
                        {getWords(
                          props.result_moth_words.activeWords["C"].concat(
                            props.result_moth_words.activeWords["D"]
                          )
                        )}
                      </tbody>
                    </table>
                  </div>
                  <Image
                    className="moth-result-chosen"
                    src={require(`./../assets/images/results/CD_pattern/pattern${CD_number}.png`)}
                  />
                  <div className="moth-text">{result_texture_text} </div>
                </div>
                <div className="moth-result-chosen-container">
                  <div className="moth-result-chosen-title">
                    <div className="moth-result-chosen-text">燃燒程度</div>
                    <span className="moth-result-chosen-arrow">
                      <Image
                        className="moth-result-dobule-arrow"
                        src={require("./../assets/images/results/double_arrow.png")}
                      />
                    </span>
                    <div className="moth-result-chosen-text">行為目的</div>
                  </div>

                  <div className="moth-words-table">
                    <table style={{ marginBottom: "0px" }}>
                      <tbody>
                        {getWords(
                          props.result_moth_words.activeWords["E"].concat(
                            props.result_moth_words.activeWords["F"]
                          )
                        )}
                      </tbody>
                    </table>
                  </div>
                  <Image
                    className="moth-result-chosen"
                    src={require(`./../assets/images/results/EF_burn/burn${EF_number}.png`)}
                  />
                  <div className="moth-text">{result_burn_text} </div>
                </div>
              </section>
              <section className="result-icon-section">
                <div className="result-icon-container">
                  {/* <a
                    onClick={() => {
                      navigator.clipboard.writeText("https://mothinstitute.tw");
                    }}
                  >
                    <Image
                      className="result-icon"
                      src={require("./../assets/images/results/icons/copylink.png")}
                    />
                  </a> */}

                  <Link to="/">
                    <Image
                      className="result-icon"
                      src={require("./../assets/images/results/icons/home.png")}
                    />
                  </Link>
                  <a href="https://instagram.com/moth_institute?igshid=13hcio1pigiej">
                    <Image
                      className="result-icon"
                      src={require("./../assets/images/results/icons/ig.png")}
                    />
                  </a>
                </div>
              </section>
            </div>
          </CSSTransition>
        </div>
      </div>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    result_moth_words: state.moth_words,
  };
};
export default connect(mapStateToProps)(Result);
