import * as act from ".";

export const testactiontrue = () => (dispatch) => {
  return dispatch({
    type: act.TEST_ACTION_FALSE,
    payload: true,
  });
};

export const testactionfalse = () => (dispatch) => {
  return dispatch({
    type: act.TEST_ACTION_FALSE,
    payload: false,
  });
};

export const activesupdate = (l) => (dispatch) => {
  return dispatch({
    type: act.ACTIVES_UPDATE,
    payload: l,
  });
};

export const active_nums_update = (object) => (dispatch) => {
  return dispatch({
    type: act.ACTIVE_NUMS_UPDATE,
    payload: object,
  });
};

export const active_words_update = (object) => (dispatch) => {
  return dispatch({
    type: act.ACTIVE_WORDS_UPDATE,
    payload: object,
  });
};
