import React, { useState, useEffect } from "react";
import Button from "react-bootstrap/Button";
import Image from "../components/elements/Image";
import { CSSTransition } from "react-transition-group";
import { connect, useDispatch } from "react-redux";
import {
  activesupdate,
  active_nums_update,
  active_words_update,
} from "../actions/moth_words.action";

import { A, B, C, D, E, F } from "../data/MothWords";

const Words = (props) => {
  const dispatch = useDispatch();
  const [show, setShow] = useState(false);
  const [toggle, setToggle] = useState(false);
  const [alerting, setAlerting] = useState(false);
  useEffect(() => {
    setShow(true);
  }, []);

  function handleActive(word) {
    if (word.active) {
      word.active = false;
    } else {
      word.active = true;
    }

    // change it to set to use set's function.
    const actives_set = new Set(props.actives);
    if (actives_set.has(word.name)) {
      actives_set.delete(word.name);
    } else {
      actives_set.add(word.name);
    }
    dispatch(activesupdate(Array.from(actives_set)));

    setToggle(!toggle);
  }

  function handleSubmit() {
    const A_active = A.filter((d) => d.active === true);
    const B_active = B.filter((d) => d.active === true);
    const C_active = C.filter((d) => d.active === true);
    const D_active = D.filter((d) => d.active === true);
    const E_active = E.filter((d) => d.active === true);
    const F_active = F.filter((d) => d.active === true);

    props.activeWords["A"] = A_active;
    props.activeWords["B"] = B_active;
    props.activeWords["C"] = C_active;
    props.activeWords["D"] = D_active;
    props.activeWords["E"] = E_active;
    props.activeWords["F"] = F_active;

    props.activeNums["A"] = A_active.length;
    props.activeNums["B"] = B_active.length;
    props.activeNums["C"] = C_active.length;
    props.activeNums["D"] = D_active.length;
    props.activeNums["E"] = E_active.length;
    props.activeNums["F"] = F_active.length;

    dispatch(active_words_update(props.activeWords));
    dispatch(active_nums_update(props.activeNums));

    if (props.activeNums["C"] + props.activeNums["D"] === 0) {
      C.forEach((word) => (word.active = true));
      D.forEach((word) => (word.active = true));
      setToggle(!toggle);
      setAlerting(true);
      setTimeout(() => {
        C.forEach((word) => (word.active = false));
        D.forEach((word) => (word.active = false));
        setAlerting(false);
      }, 200);
    } else {
      if (!alerting) {
        props.history.push("/result");
      }
    }
  }

  function getWords(L, activeOnly = false) {
    if (activeOnly) {
      const L_active = L.filter((d) => d.active === true);
      if (L_active.length === 0) {
        return <></>;
      } else
        return (
          <tr>
            <td>
              {L_active.map((word, idx) => (
                <React.Fragment key={word.name}>
                  <Button
                    className="moth-words-button"
                    variant="outline-dark"
                    active={word.active}
                    onClick={() => handleActive(word)}
                  >
                    {word.name}
                  </Button>
                  &thinsp;&thinsp;
                </React.Fragment>
              ))}
            </td>
          </tr>
        );
    } else {
      return (
        <tr>
          <td>
            {L.map((word, idx) => (
              <React.Fragment key={word.name}>
                <Button
                  className="moth-words-button"
                  variant="outline-dark"
                  active={word.active}
                  onClick={() => handleActive(word)}
                >
                  {word.name}
                </Button>
                &thinsp;&thinsp;
              </React.Fragment>
            ))}
          </td>
        </tr>
      );
    }
  }

  return (
    <>
      <CSSTransition in={show} timeout={1000} classNames="landing">
        <main className="site-content">
          <section style={{ height: "10vh" }}></section>
          <div className="container-sm word-header-arrow-text-container">
            <span className="header-arrow-container">
              <Image
                className="moth-words-arrow"
                src={require(`./../assets/images/header_arrow.png`)}
              />
              <Image
                className="moth-words-arrow"
                src={require(`./../assets/images/header_arrow.png`)}
              />
            </span>
            <div className="header-text-container">
              這件事對你來說與哪些因素密切相關呢？
              <br />
              在以下分類中尋找一些影響你做決定的因素吧！
            </div>
          </div>

          <section style={{ height: "70vh" }}>
            <div className="container-sm">
              <div className="ta-l moth-words-class">起源</div>
              <div className="moth-words-table">
                <table>
                  <tbody>
                    {getWords(A)}
                    {getWords(B)}
                  </tbody>
                </table>
              </div>
              <div className="ta-l moth-words-class">互動關係</div>
              <div className="moth-words-table">
                <table>
                  <tbody>
                    {getWords(C)}
                    {getWords(D)}
                  </tbody>
                </table>
              </div>
              <div className="ta-l moth-words-class">行為目的</div>
              <div className="moth-words-table">
                <table>
                  <tbody>
                    {getWords(E)}
                    {getWords(F)}
                  </tbody>
                </table>
              </div>
              <div className="ta-l moth-words-class">你選擇了</div>
              <div className="moth-words-table">
                <table>
                  <tbody>
                    {getWords(A.concat(B), true)}
                    {!alerting && getWords(C.concat(D), true)}
                    {getWords(E.concat(F), true)}
                  </tbody>
                </table>
              </div>
            </div>
          </section>
          <section style={{ height: "10vh" }}>
            <div className="moth-footer-container">
              <Button
                variant="outline-dark"
                className="moth-footer-button"
                onClick={() => {
                  handleSubmit();
                }}
              >
                找出屬於我的蛾！
              </Button>
            </div>
          </section>
        </main>
      </CSSTransition>
    </>
  );
};

// map the store's state to the component's (Words) prop
// this state is Words' state
// map state to props so that Result can use this state from store through props.
// left : self's props, right: store's state
const mapStateToProps = (state) => {
  return {
    actives: state.moth_words.actives,
    activeNums: state.moth_words.activeNums,
    activeWords: state.moth_words.activeWords,
  };
};

export default connect(mapStateToProps)(Words);
