import parse from "html-react-parser";
import React, { useEffect, useState } from "react";
import { CSSTransition } from "react-transition-group";
import Image from "../components/elements/Image";
//import landing_anima from "./../assets/animation/landing_light.gif";
//import landing_anima from "./../assets/animation/before.gif";
import landing_anima from "./../assets/animation/animation3.gif";
import logo from "./../assets/images/landing/logo.png";
const landing_text = parse("如果你是一隻蛾，<br/>你會是怎樣的一隻蛾？");

const window_width = window.innerWidth;
const window_height = window.innerHeight;

const Landing = (props) => {
  const [showText, setShowText] = useState(false);
  const [showImage, setShowImage] = useState(false);

  useEffect(() => {
    // pre load background gif
    document.body.style.backgroundImage = `url(${landing_anima})`;
    document.body.style.backgroundSize = 0;
    setShowImage(true); // 顯示LOGO (0s)

    // callback hell, but it works
    setTimeout(() => {
      setShowImage(false);
      setTimeout(() => {
        document.body.style.backgroundImage = `url(${landing_anima})`;
        if (window_height >= window_width) {
          document.body.style.backgroundSize = "auto 50%";
        } else {
          document.body.style.backgroundSize = "auto 50%";
        }
        setTimeout(() => {
          setShowText(true);
          setTimeout(() => {
            document.body.style.backgroundSize = 0;
            window.location = "/moth/#/event";
          }, 2000); //跳轉 (8000ms)
        }, 2500); //文字出現 (6000ms)
      }, 1800); //背景動畫 (4000ms)
    }, 2500); // LOGO消失 (2000ms)
  }, []);

  return (
    <>
      {/* small hacking for preload image */}
      {/* <div id="preload" style={{ display: "none" }}>
        <div style={{ backgroundImage: `url(${landing_anima})` }}></div>
      </div> */}

      <CSSTransition in={showImage} timeout={2000} classNames="landing">
        <Image className="landing-logo" src={logo} />
      </CSSTransition>

      <CSSTransition in={showText} timeout={2000} classNames="landing-text">
        <section className="landing-text-container">
          <div className="moth-text landing-text">{landing_text}</div>
        </section>
      </CSSTransition>
    </>
  );
};

export default Landing;
