import React from "react";
import { Route } from "react-router-dom";

const AppRoute = ({
  component: Component,
  layout: Layout,
  pageNumber,
  ...rest
}) => {
  Layout = Layout === undefined ? (props) => <>{props.children}</> : Layout;

  return (
    <Route
      {...rest}
      render={(props) => (
        <>
          <Layout pageNumber={pageNumber}>
            <Component {...props} />
          </Layout>
        </>
      )}
    />
  );
};

export default AppRoute;
